import { useCallback, useEffect, useState } from "react";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useInView } from "react-intersection-observer";
import { useFeedbackSubjectTeacherList } from "../../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { useTranslation } from "react-i18next";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import StudentFeedbackAllStaffCard from "./StudentFeedbackAllStaffCard";
const StudentFeedbackAllStaff = ({ instituteId, ifid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { feedbackSubjectTeacherList, feedbackSubjectTeacherListLoading } =
    useFeedbackSubjectTeacherList({
      data: {
        ifid: ifid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !ifid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (feedbackSubjectTeacherList?.list?.length) {
      let obj = nextPage(feedbackSubjectTeacherList?.list?.length);
      setState(() => obj);
    }
  }, [feedbackSubjectTeacherList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  return (
    <>
      <div className={style.fm_header_container}>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
              />
            </div>
          </section>
        </div>
      </div>
      {feedbackSubjectTeacherList?.list?.map((staff, index) =>
        feedbackSubjectTeacherList?.list?.length === index + 1 ? (
          <div key={staff?._id} ref={ref}>
            <StudentFeedbackAllStaffCard staff={staff} />
          </div>
        ) : (
          <StudentFeedbackAllStaffCard staff={staff} key={staff?._id} />
        )
      )}
      {feedbackSubjectTeacherListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        feedbackSubjectTeacherList?.list?.length === 0 && (
          <EmptyMessage title="No staff fuond related this search." />
        )
      ) : (
        feedbackSubjectTeacherList?.list?.length === 0 && (
          <EmptyMessage title="No staff." />
        )
      )}
    </>
  );
};

export default StudentFeedbackAllStaff;
