import { useTranslation } from "react-i18next";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import style from "../InstituteFeedback.module.css";
import { Link, useLocation, useNavigate, useParams } from "react-router-dom";
import { useState } from "react";
import StudentFeedbackCardMenu from "./StudentFeedbackCardMenu";
import dayjs from "dayjs";
const StudentFeedbackCard = ({ feedback, onRefetch }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  const navigate = useNavigate();

  const [menu, setMenu] = useState(false);

  const onToggle = (e) => {
    if (e) e.preventDefault();
    setMenu((pre) => !pre);
  };

  const onAnalytic = (e) => {
    e.preventDefault();
    navigate(
      `/q/${params.username}/institute/member/feedback?a=feedback-analytic`,
      {
        state: {
          ...getQuery.state,
          ifid: feedback?._id,
        },
      }
    );
  };

  return (
    <>
      <Link
        to={`/q/${params.username}/institute/member/feedback?a=feedback-detail`}
        state={{
          ...getQuery.state,
          ifid: feedback?._id,
          feedbackName: feedback?.feedback_name,
        }}
      >
        <div className={style.sf_card}>
          <div className={style.sf_card_contatiner}>
            <img
              src={`${assestsFeedUrl}/feed-user-avatar.svg`}
              loading="lazy"
              alt="avatar"
            />

            <div className={style.sf_card_contatiner_inner}>
              <h6>{feedback?.feedback_name ?? ""}</h6>
              <p>
                {" "}
                {t("question")} {": "} {feedback?.question_count ?? 0}
              </p>
              <p>
                {" "}
                {t("date")} {": "}{" "}
                {dayjs(feedback?.created_at)?.format("DD/MM/YYYY")}
                {feedback?.feedback_close_at ? (
                  <>
                    {","}
                    {t("to")}{" "}
                    {dayjs(feedback?.feedback_close_at)?.format("DD/MM/YYYY")}
                  </>
                ) : null}
              </p>
              {feedback?.evaluation ? (
                <p>
                  {" "}
                  {t("total_feedback_given")} {": "}{" "}
                  {feedback?.feedback_given_student_count}/
                  {feedback?.feedback_notify_count}
                </p>
              ) : null}
            </div>
          </div>
          <div className={style.sf_card_contatiner}>
            <CreateButton
              label={t("analytic")}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
                height: "fit-content",
              }}
              onAction={onAnalytic}
            />
            {feedback?.evaluation ? null : (
              <img
                src={`${assestsUserFinanceUrl}/menu.svg`}
                alt="menu icon"
                onClick={onToggle}
                title="Menu"
                style={{
                  cursor: "pointer",
                  height: "1.5rem",
                }}
              />
            )}
          </div>
        </div>
      </Link>
      {menu && (
        <StudentFeedbackCardMenu
          onClose={onToggle}
          feedback={feedback}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default StudentFeedbackCard;
