import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import { assestsUtilityUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import HorizontalTabWrapper from "../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import StudentFeedbackAllStaff from "../StudentFeedbackAllStaff";
import StudentFeedbackDetailQuestion from "./StudentFeedbackDetailQuestion";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../Ui/Button/CreateButton";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../../Utils/Functions/localTime";
import TakeStudentFeedback from "./TakeStudentFeedback";
import { useState } from "react";
import { useFeedbackDetailStudentFeedback } from "../../../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
const StudentFeedbackDetail = ({ instituteId }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const params = useParams();
  const [add, setAdd] = useState(false);

  const {
    feedbackDetailStudentFeedback,
    feedbackDetailStudentFeedbackRefetch,
  } = useFeedbackDetailStudentFeedback({
    data: {
      ifid: getQuery.state?.ifid,
    },
    skip: !getQuery.state?.ifid,
  });
  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onBack = () => {
    navigate(`/q/${params.username}/institute/member/feedback`, {
      state: getQuery.state,
    });
  };

  // console.info("sdfdsfd", feedbackDetailStudentFeedback);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div className={style.fm_header_start_container}>
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onBack}
              />
              <h6>{getQuery.state?.feedbackName ?? ""}</h6>
            </div>
            <div>
              {feedbackDetailStudentFeedback?.evaluation ? (
                <p className={style.fm_header_paragraph}>
                  {t("taken_on")} {": "}{" "}
                  {dayjs(
                    feedbackDetailStudentFeedback?.feedback_take_date
                  ).format("DD/MM/YYYY")}
                  {", "}{" "}
                  {localTimeFormat(
                    feedbackDetailStudentFeedback?.feedback_take_date,
                    "LT"
                  )}
                </p>
              ) : (
                <CreateButton
                  label={t("take_feedback")}
                  customStyle={{
                    margin: "0",
                    padding: "0.4rem 1rem",
                    width: "10rem",
                  }}
                  onAction={onToggle}
                />
              )}
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={`/q/${params.username}/institute/member/feedback?a=feedback-detail${tab.urlKey}`}
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginBlock: "0",
          }}
        />
        <UserMemberContentWrapper>
          {!searchParams.get("n") ? (
            <StudentFeedbackAllStaff
              instituteId={getQuery.state?.instituteId}
              ifid={getQuery.state?.ifid}
            />
          ) : searchParams.get("n") === "question" ? (
            <StudentFeedbackDetailQuestion
              ifid={getQuery.state?.ifid}
              questionCount={feedbackDetailStudentFeedback?.question_count}
              optionCount={
                feedbackDetailStudentFeedback?.how_many_question_option
              }
              onRefetch={feedbackDetailStudentFeedbackRefetch}
              questionList={feedbackDetailStudentFeedback?.questions}
              isEvaluate={feedbackDetailStudentFeedback?.evaluation}
            />
          ) : null}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>

      {add && (
        <TakeStudentFeedback
          onClose={onToggle}
          optionCount={feedbackDetailStudentFeedback?.how_many_question_option}
          onRefetch={feedbackDetailStudentFeedbackRefetch}
          instituteId={instituteId}
          ifid={getQuery.state?.ifid}
        />
      )}
    </>
  );
};

export default StudentFeedbackDetail;

const tabList = [
  {
    label: "staff",
    id: "staff",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "question",
    id: "question",
    urlKey: "&n=question",
    groupIs: ["question"],
  },
];
